"use client";

import { useRouter } from "next/navigation";
import { useState } from "react";
import { products } from "../data/products";

export default function ProductTabNavigator() {
  const router = useRouter();
  const [activeProduct, setActiveProduct] = useState<string | null>(null);

  return (
    <div className="w-full bg-gray-100 p-4 mb-8 grid grid-cols-1 sm:grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
      {products.map((product) => (
        <button
          key={product.id}
          onClick={() => {
            setActiveProduct(product.id);
            router.push(`/products/${product.slug}`);
          }}
          className={`px-4 py-2 text-sm md:text-base text-left rounded-lg transition-colors duration-300 shadow-sm w-full focus:outline-none 
            ${activeProduct === product.id ? "bg-[#0E9696] text-white" : "bg-white hover:bg-[#0E9696] hover:text-white"}
          `}
        >
          {product.name}
        </button>
      ))}
    </div>
  );
}
